.home,
.services,
.products,
.sign-up {
   display: flex;
   height: 90vh;
   align-items: center;
   justify-content: center;
   font-size: 3rem;
}

.services {
   /* background-image: url('/images/img-2.jpg'); */
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;
   color: #fff;
   font-size: 100px;
}

.products {
   /* background-image: url('/images/img-1.jpg'); */
   background-position: center;
   background-size: fill;
   background-repeat: no-repeat;
   color: #fff;
   font-size: 100px;
}

.sign-up {
   /* background-image: url('/images/img-8.jpg'); */
   background-position: center;
   background-size: cover;
   background-repeat: no-repeat;
   color: #fff;
   font-size: 100px;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
   -webkit-appearance: none;
   margin: 0;
}

/* Firefox */
input[type='number'] {
   -moz-appearance: textfield;
}

.createJob_page {
   /* @media only screen and (min-width: 600px) {
      padding: 5rem;
   } */
   background-image: linear-gradient(135deg, #81ffef 10%, #f067b4 100%);
}

.createJob_jobInfo {
   display: flex;
   justify-content: space-evenly;
   border-radius: 30px;
   flex-direction: column;
   /* box-shadow: 20px 20px 50px 10px rgb(234, 0, 255); */
   padding: 2rem;
   background: rgb(255, 255, 255);
}

.createJob_mainForm {
   margin: 2rem;
   display: flex;
   justify-content: space-evenly;
   flex-flow: row wrap;
   align-content: flex-start;
   padding: 2rem;
}
.createJob_credit-section {
   background: rgb(255, 255, 255);
   border-radius: 30px;

   padding: 2rem;
   display: flex;
   flex-direction: column;
   justify-content: space-evenly;
}
.createJob_submitSection {
   color: white;
   display: flex;
   justify-content: center;
   padding: 1rem;
}

.createJob_imageSection {
   min-height: 500px;
   max-height: 500px;
   border-radius: 30px;
   padding: 1rem;
   margin-left: 2rem;
   margin-right: 2rem;
   background: rgb(255, 255, 255);
   overflow: auto;
}

.createJob_imagePrev {
   display: flex;

   flex-direction: row;
   flex-wrap: wrap;
   max-width: 300px;
}

.btn {
   padding: 8px 20px;
   border-radius: 2px;
   outline: none;
   border: none;
   cursor: pointer;
}

.btn-hover {
   background-image: linear-gradient(
      to right,
      #9733ee,
      #da22ff,
      #da22ff,
      #9733ee
   );
   box-shadow: 0 3px 10px 0 #190030;
   width: 200px;
   font-size: 16px;
   font-weight: 600;
   color: #fff;
   cursor: pointer;
   margin: 20px;
   height: 55px;
   text-align: center;
   border: none;
   background-size: 300% 100%;

   border-radius: 50px;
   transition: all 0.4s ease-in-out;
}

.btn-hover:hover {
   background-position: 100% 0;
   transition: all 0.4s ease-in-out;
}

.btn-hover:focus {
   outline: none;
}

.btn--outline {
   background-color: transparent;
   color: #fff;
   padding: 8px 20px;
   box-shadow: 0 3px 10px 0 #21003d;
   border: 2px solid var(--primary);
   border-radius: 50px;
   transition: all 0.3s ease-out;
}

.btn--small {
   padding: 6px 15px;
   font-size: 16px;
   width: 110px;
   height: 45px;
}

.btn--medium {
   padding: 8px 20px;
   font-size: 18px;
}

.btn--large {
   padding: 12px 26px;
   font-size: 20px;
}

.btn--outline:hover {
   transition: all 0.3s ease-out;
   background: #da22ff;
   color: #ffffff;
   transition: 250ms;
}

.footer-container {
   background-color: #242424;
   padding: 4rem 0 2rem 0;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.footer-subscription {
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   text-align: center;

   margin-bottom: 24px;
   padding: 24px;
   color: #fff;
}

.footer-subscription > p {
   font-family: 'Montserrat', sans-serif;
}

.footer-subscription-heading {
   margin-bottom: 24px;
   font-size: 24px;
}

.footer-subscription-text {
   margin-bottom: 24px;
   font-size: 20px;
}

.footer-input {
   padding: 8px 20px;
   border-radius: 2px;
   margin-right: 10px;
   outline: none;
   border: none;
   font-size: 18px;
   margin-bottom: 16px;
   border: 1px solid #fff;
}

.footer-links {
   width: 100%;
   max-width: 1000px;
   display: flex;
   justify-content: center;
}

.footer-link-wrapper {
   display: flex;
}

.footer-link-items {
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   margin: 16px;
   text-align: left;
   width: 160px;
   box-sizing: border-box;
}

.footer-link-items h2 {
   margin-bottom: 16px;
}

.footer-link-items > h2 {
   color: #fff;
}

.footer-link-items a {
   color: #fff;
   text-decoration: none;
   margin-bottom: 0.5rem;
}

.footer-link-items a:hover {
   color: #e9e9e9;
   transition: 0.3s ease-out;
}

.footer-email-form h2 {
   margin-bottom: 2rem;
}

.footer-input::-webkit-input-placeholder {
   color: #b1b1b1;
}

.footer-input:-ms-input-placeholder {
   color: #b1b1b1;
}

.footer-input::placeholder {
   color: #b1b1b1;
}

/* Social Icons */
.social-icon-link {
   color: #fff;
   font-size: 24px;
}

.social-media {
   max-width: 1000px;
   width: 100%;
}

.social-media-wrap {
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 90%;
   max-width: 1000px;
   margin: 40px auto 0 auto;
}

.social-icons {
   display: flex;
   justify-content: space-between;
   align-items: center;
   width: 240px;
}

.social-logo {
   color: #fff;
   justify-self: start;
   cursor: pointer;
   text-decoration: none;
   font-size: 2rem;
   display: flex;
   align-items: center;
}

.website-rights {
   color: #fff;
   margin-bottom: 16px;
   font-family: 'Montserrat', sans-serif;
}

@media screen and (max-width: 820px) {
   .footer-links {
      padding-top: 2rem;
   }

